import { GET_FAVORITE_LIST } from "@/api/api";
export default {
  name: "favoritedJobsDashboard",

  data() {
    return {
      page: 1,
      limit: 10000,
      favoriteData: [],
      totalNum: 0
    };
  },

  mounted() {
    this.getFavoriteList(this.page, this.limit);
  },

  methods: {
    getFavoriteList(page, limit) {
      let params = {
        token: localStorage.getItem('token'),
        page: page,
        limit: limit
      };
      GET_FAVORITE_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.favoriteData = res.message.data; // console.log(res.message.data)

          this.totalNum = res.message.total;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    }

  }
};