import doubtingImg from '@/assets/newHome/match/doubting.png';
import funfareImg from '@/assets/newHome/match/funfare.png';
import thumbUpImg from '@/assets/newHome/match/thumb_up.png';
import { EDUCATOR_JOB_MATCH_LIST, JOB_FEATURED_LIST } from "@/api/api";
export default {
  name: "dailyJobMatch",

  data() {
    return {
      doubtingImg,
      funfareImg,
      thumbUpImg,
      jobFeaturedData: []
    };
  },

  mounted() {
    this.getEducatorJobMatchingList(); // this.getJobFeaturedList()
  },

  methods: {
    turnJobDetail(id) {
      this.$router.push({
        path: '/jobs',
        query: {
          id: id
        }
      });
    },

    getJobFeaturedList() {
      let params = {
        ad_type: 2
      };
      JOB_FEATURED_LIST(params).then(res => {
        console.log(res);

        if (res.code === 200) {
          this.jobFeaturedData = res.message;
        } else {
          console.log(res.msg);
        }
      }).catch(err => {
        console.log(err);
      });
    },

    getEducatorJobMatchingList() {
      let params = {
        user_id: localStorage.getItem('uid')
      };
      EDUCATOR_JOB_MATCH_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.jobFeaturedData = res.message;
        }
      }).catch(err => {
        console.log(err);
      });
    }

  }
};